
import api from '../api/api'
import env from '../config/env'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import ViewBox from 'vux/src/components/view-box/index.vue'

// style
import '../style/iconfont.css'

export default {
    name: 'main',
    data() {
        return {
            // tabindex
            selectedIndex: 0,
        }
    },
    mounted() {
        //this.initData()
    },
    created() {
        // pathtab
        const routePath = this.$store.state.route.path.split(/\//) && this.$store.state.route.path.split(/\//)[1];
        if (routePath === 'shop') {
            this.selectedIndex = 0
        } else if (routePath === 'user') {
            this.selectedIndex = 1
        } else if(routePath === 'notice') {
            this.selectedIndex = 2
        } else {
            this.selectedIndex = sessionStorage.tabIndex
        }
    },
    methods: {
        handler(routerName) {
            // tab index
            sessionStorage.tabIndex = this.selectedIndex
            this.$router.replace({
                path: "/" + routerName
            })
        }
    },
    components: {
        Tabbar,
        TabbarItem,
        ViewBox
    }
}



